.a-button {
  display: inline-flex;
  letter-spacing: 0;
  padding: 0;
  border-radius: 0;
  cursor: pointer;

  &:disabled {
    cursor: default;
  }

  &__label {
    padding: rem(12px) rem(16px) rem(12px) 0;
  }

  &__icon + &__label {
    text-align: left;
  }

  &__icon {
    font-size: rem(24px);
    padding: rem(12px) rem(8px) rem(12px) rem(14px);
  }

  &.-fixed {
    width: rem(128px);
  }

  &.-fixed &__label {
    display: block;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &.-without-label &__icon {
    margin: auto;
    padding: rem(12px);
  }

  &.-without-icon &__label {
    margin: auto;
    padding: rem(12px) rem(16px);
  }

  &--primary {
    background-color: $bosch-blue-50;
    border: 0;
    color: $bosch-white;

    &:hover {
      background-color: $bosch-blue-40;
    }

    &:active {
      background-color: $bosch-blue-30;
    }

    &:disabled {
      background-color: $bosch-blue-90;
      color: $bosch-blue-70;

      @include background-secondary {
        background-color: $bosch-gray-70;
        color: $bosch-gray-95;
      }

      @include background-tertiary {
        background-color: $bosch-gray-90;
        color: white;
      }
    }
  }

  &--secondary {
    border: 1px solid $bosch-blue-50;
    color: $bosch-blue-50;
    background: transparent;

    &:hover {
      color: $bosch-blue-40;
      border-color: $bosch-blue-40;
      background-color: $bosch-blue-90;
    }

    &:active {
      border-color: $bosch-blue-30;
      color: $bosch-blue-30;
      background-color: $bosch-blue-80;
    }

    &:disabled {
      background-color: transparent;
      border-color: $bosch-blue-80;
      color: $bosch-blue-80;

      @include background-secondary {
        border-color: $bosch-blue-70;
        color: $bosch-blue-70;
      }

      @include background-tertiary {
        border-color: $bosch-blue-80;
        color: $bosch-blue-80;
      }
    }
  }

  &--tertiary {
    border: 0;
    color: $bosch-blue-50;
    background: transparent;

    &:hover {
      background-color: $bosch-blue-90;
      color: $bosch-blue-40;
    }

    &:active {
      background-color: $bosch-blue-80;
      color: $bosch-blue-30;
    }

    &:disabled {
      background-color: transparent;
      color: $bosch-blue-80;

      @include background-secondary {
        color: $bosch-blue-70;
      }

      @include background-tertiary {
        color: $bosch-blue-80;
      }
    }
  }

  &--close {
    align-items: center;
    justify-content: center;
    background-color: transparent;
    border: 0;
    height: rem(48px);
    width: rem(48px);
    display: flex;

    &::before {
      background: $bosch-black;
      content: '';
      height: rem(1px);
      width: rem(22px);
      transform: rotate(45deg);
      position: absolute;
    }

    &::after {
      background: $bosch-black;
      content: '';
      height: rem(1px);
      width: rem(22px);
      transform: rotate(-45deg);
      position: absolute;
    }

    &:hover {
      &::before,
      &::after {
        background: $bosch-gray-30;
      }
    }

    &:active {
      &::before,
      &::after {
        background: $bosch-gray-50;
      }
    }

    &:disabled {
      &::before,
      &::after {
        background: $bosch-gray-70;
      }
    }
  }
}

