.a-searchfield {
  position: relative;

  input[type='search'] {
    background-color: $bosch-gray-95;
    border: 0;
    border-bottom: rem(1px) solid $bosch-black;
    height: rem(48px);
    padding: 0 rem(12px) 0 rem(48px);
    width: rem(200px);

    &::placeholder {
      color: rgba($color: $bosch-black, $alpha: 0.5);
    }

    @include background-secondary {
      background-color: $bosch-white;
    }

    @include background-tertiary {
      background-color: $bosch-gray-95;
    }

    &:hover {
      background-color: $bosch-gray-90;
    }

    &:active {
      background-color: $bosch-gray-80;
    }

    &:disabled {
      border-bottom-color: $bosch-gray-70;
      pointer-events: none;

      @include background-secondary {
        background-color: $bosch-white;
      }

      @include background-tertiary {
        background-color: $bosch-gray-70;
      }

      & + label::before {
        color: $bosch-gray-70;
      }
    }
  }

  label {
    left: 0;
    position: absolute;
    top: 25%;

    &::before {
      content: '\e235';
      font-family: 'Bosch-Icon', sans-serif;
      font-size: rem(21px);
      margin: auto rem(12px);
    }
  }
}
