.a-tooltip {
  position: relative;

  /* stylelint-disable-next-line a11y/content-property-no-static-value */
  &:hover::before {

    @include box-shadow;

    position: absolute;
    max-width: rem(256px);
    margin: rem(12px);
    padding: rem(10px) rem(14px);
    color: $bosch-black;
    background-color: $bosch-white;
    transform-origin: top left;
    transform: translateX(-rem(14px)) translateY(calc(-100% - rem(16px)));
    content: attr(data-tooltip);
    z-index: 9999;
  }

  &--success {
    &:hover::before {
      background-color: $bosch-green-95;
      box-shadow: 0 0 1rem rgba($color: $bosch-green-40, $alpha: 0.25);
      color: $bosch-green-40;
    }
  }

  &--warning {
    &:hover::before {
      background-color: $bosch-yellow-95;
      box-shadow: 0 0 1rem rgba($color: $bosch-black, $alpha: 0.25);
      color: $bosch-yellow-40;
    }
  }

  &--error {
    &:hover::before {
      background-color: $bosch-red-95;
      box-shadow: 0 0 1rem rgba($color: $bosch-black, $alpha: 0.25);
      color: $bosch-red-40;
    }
  }

  &.-dynamic-width {
    &:hover::before {
      max-width: none;
    }
  }

  &.-fixed-width {
    &:hover::before {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      width: rem(160px);
    }
  }
}
