@font-face {
  font-family: 'Bosch-Icon';
  src: url('../assets/fonts/bosch_icon.woff2') format('woff2'), url('../assets/fonts/bosch_icon.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

[class^='boschicon-'],
[class*=' boschicon-'] {

  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'Bosch-Icon', sans-serif !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

