.a-pin {
  svg {
    cursor: pointer;

    &:hover {
      path:nth-of-type(1) {
        fill: $bosch-blue-30;
      }
    }

    &:active {
      path:nth-of-type(1) {
        fill: $bosch-blue-20;
      }
    }
  }

  &.-disabled {
    pointer-events: none;

    svg path:nth-of-type(1) {
      fill: $bosch-gray-80;
      stroke: $bosch-gray-95;

      @include background-secondary {
        fill: $bosch-gray-70;
      }

      @include background-tertiary {
        fill: $bosch-gray-80;
      }
    }
  }

  &--cluster {
    align-items: center;
    background-color: $bosch-blue-40;
    border: 1px solid $bosch-white;
    border-radius: 50%;
    color: $bosch-white;
    cursor: pointer;
    display: flex;
    height: rem(48px);
    justify-content: center;
    width: rem(48px);

    &:hover {
      background-color: $bosch-blue-30;
    }

    &:active {
      background-color: $bosch-blue-20;
    }

    &.-disabled {
      background-color: $bosch-gray-80;
      border-color: $bosch-gray-95;
      pointer-events: none;

      @include background-secondary {
        background-color: $bosch-gray-70;
      }

      @include background-tertiary {
        background-color: $bosch-gray-80;
      }
    }
  }
}
