.-primary {
  background: $bosch-white;
}

.-secondary {
  background: $bosch-gray-95;
}

@mixin box-shadow {
  box-shadow: 0 0 0.5rem 0.1rem rgba($color: $bosch-black, $alpha: 0.25);
}

.-floating {
  background: $bosch-white;

  @include box-shadow;
}

.-contrast {
  background: $bosch-gray-20;
}

@mixin background-dimmed {
  background: rgba($bosch-gray-50, 0.25);
  backdrop-filter: blur(rem(4px));
}

.-dimmed {

  @include background-dimmed;
}
