a,
a:visited {
  text-decoration: none;
  color: $bosch-blue-50;

  &:hover {
    color: $bosch-blue-40;
    text-decoration: underline;

    &::after {
      border-top-color: $bosch-blue-40;
      border-right-color: $bosch-blue-40;
    }
  }

  &:active {
    color: $bosch-blue-30;

    &::after {
      border-top-color: $bosch-blue-30;
      border-right-color: $bosch-blue-30;
    }
  }
}

/* stylelint-disable no-descending-specificity */
.-disabled a,
a:disabled {
  pointer-events: none;
  color: $bosch-blue-80;

  &::after {
    border-top-color: $bosch-blue-80;
    border-right-color: $bosch-blue-80;
  }

  @include background-secondary {
    color: $bosch-blue-70;

    &::after {
      border-top-color: $bosch-blue-70;
      border-right-color: $bosch-blue-70;
    }
  }

  @include background-tertiary {
    color: $bosch-blue-80;

    &::after {
      border-top-color: $bosch-blue-80;
      border-right-color: $bosch-blue-80;
    }
  }
}

.a-link--simple,
.a-link--primary {
  display: inline-block;

  a {
    display: flex;
  }
}

.a-link--primary {
  a {
    &::after {
      content: '';
      border-top: rem(1px) solid $bosch-blue-50;
      border-right: rem(1px) solid $bosch-blue-50;
      position: relative;
      width: .5rem;
      height: .5rem;
      margin-left: .25rem;
      transform: translateY(.465rem) rotate(45deg);
      /* @TODO find a better solution for IE 11 */
      -ms-transform-origin-y: rem(7px);
    }
  }
}
