.a-radio-button {
  position: relative;
  display: inline-block;

  input {
    display: none;
  }

  label {
    padding-left: rem(32px);
    line-height: 1.5;
    font-size: rem(16px);
    cursor: pointer;

    &::before {
      position: absolute;
      content: '';
      width: rem(24px);
      height: rem(24px);
      background: $bosch-gray-90;
      left: 0;
      top: 0;
      border-radius: rem(12px);

      @include background-secondary {
        background: $bosch-gray-75;
      }

      @include background-tertiary {
        background: $bosch-gray-90;
      }
    }

    &:hover::before,
    &:focus::before {
      background: $bosch-gray-80;

      @include background-secondary {
        background: $bosch-gray-65;
      }

      @include background-tertiary {
        background: $bosch-gray-80;
      }
    }

    &:active::before {
      background: $bosch-gray-70;

      @include background-secondary {
        background: $bosch-gray-55;
      }

      @include background-tertiary {
        background: $bosch-gray-70;
      }
    }
  }

  // Input is disabled
  input:disabled ~ label {
    cursor: default;
    color: $bosch-gray-80;

    &::before {
      background: $bosch-gray-80;
    }

    @include background-secondary {
      color: $bosch-gray-75;

      &::before {
        background: $bosch-gray-75;
      }
    }

    @include background-tertiary {
      color: $bosch-gray-80;

      &::before {
        background: $bosch-gray-80;
      }
    }
  }

  // Input is checked
  input:checked ~ label {
    &::before {
      background: $bosch-blue-50;
    }

    &::after {
      background-color: $bosch-white;
      border-radius: 50%;
      content: '';
      display: inline-block;
      height: rem(12px);
      left: 0;
      position: absolute;
      top: 0;
      transform-origin: top left;
      transform: translateX(rem(6px)) translateY(rem(6px));
      width: rem(12px);
    }

    &:hover::before {
      background: $bosch-blue-40;
    }

    &:active::before {
      background: $bosch-blue-30;
    }
  }

  // Input is checked and disabled
  input:checked:disabled ~ label {
    &::before {
      background: $bosch-blue-90;
    }

    &::after {
      background: $bosch-blue-70;
    }

    @include background-secondary {
      color: $bosch-gray-70;

      &::before {
        background: $bosch-blue-85;
      }

      &::after {
        background: $bosch-blue-65;
      }
    }

    @include background-tertiary {
      color: $bosch-gray-80;

      &::before {
        background: $bosch-blue-90;
      }

      &::after {
        background: $bosch-blue-70;
      }
    }
  }
}
