.a-chip {
  display: inline-flex;
  align-items: center;
  justify-content: space-evenly;
  height: rem(32px);
  width: auto;
  border-radius: rem(16px);
  background: $bosch-gray-95;
  cursor: pointer;
  margin-right: rem(16px);

  &:hover {
    background: $bosch-gray-90;
  }

  &:active {
    background: $bosch-gray-80;
  }

  &:last-of-type {
    margin-right: 0;
  }

  @include background-secondary {
    background: $bosch-white;

    &:hover {
      background: $bosch-gray-90;
    }

    &:active {
      background: $bosch-gray-80;
    }
  }

  @include background-tertiary {
    background: $bosch-gray-95;
  }

  &.-disabled {
    pointer-events: none;
    background: $bosch-gray-95;

    @include background-secondary {
      background: $bosch-white;
    }

    @include background-tertiary {
      background: $bosch-gray-95;
    }
  }

  &.-selected {
    background: $bosch-blue-40;

    &.-disabled {
      background: $bosch-gray-80;

      @include background-secondary {
        background: $bosch-gray-70;
      }

      @include background-tertiary {
        background: $bosch-gray-80;
      }
    }

    &:hover {
      background: $bosch-blue-30;
    }

    &:active {
      background: $bosch-blue-20;
    }
  }

  &.-dragged {
    background-color: $bosch-white;

    @include box-shadow;
  }

  &--fixed {
    width: rem(97px);

    &.-image,
    &.-btnClose {
      width: rem(118px);
    }

    &.-image.-btnClose {
      width: rem(137px);
      justify-content: space-between;
    }

    .fixed-width-image-label-group {
      width: calc(100% - 1.125rem);
      display: flex;
    }
  }

  &__label {
    margin: auto rem(16px);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;

    .-btnClose & {
      margin-right: rem(8px);
    }

    .-image & {
      margin-left: rem(8px);
    }

    .-disabled & {
      color: $bosch-gray-70;
    }

    .-selected & {
      color: $bosch-white;
    }
  }

  &__image {
    width: rem(24px);
    min-width: rem(24px);
    height: rem(24px);
    margin: auto 0 auto rem(4px);
    border-radius: rem(12px);
    background-size: cover;
    background-position: 50% 50%;
  }

  &__close {
    position: relative;
    margin: auto rem(6px);
    width: rem(12px);

    &::before,
    &::after {
      content: '';
      position: absolute;
      width: rem(1px);
      height: rem(14px);
      top: rem(-7px);
      right: rem(10px);
      background: #000000;
    }

    &::after {
      transform: rotate(45deg);
    }

    &::before {
      transform: rotate(-45deg);
    }

    svg {
      display: block;
    }
  }
}
