.a-text-area {
  position: relative;
  height: rem(120px);
  width: 100%;
  overflow: hidden;

  textarea,
  &__shadow {
    background-color: $bosch-gray-90;
    border: 0;
    border-bottom: rem(1px) solid $bosch-black;
    padding: rem(12px) rem(16px) rem(12px) rem(16px);
    width: 100%;
    resize: none;
    line-height: 1.5;
    height: rem(120px);
    min-height: rem(120px);

    &::placeholder {
      color: $bosch-gray-40;
      opacity: 1;
    }

    &:hover {
      background-color: $bosch-gray-80;
    }

    &:active {
      background-color: $bosch-gray-70;
    }

    /* stylelint-disable-next-line a11y/no-outline-none */
    &:focus {
      background-color: $bosch-blue-90;
      border-bottom-color: $bosch-blue-50;
      outline: none;
    }

    &:disabled {
      border-bottom-color: $bosch-gray-70;
      pointer-events: none;

      + label::before {
        color: $bosch-gray-70;
      }

      &::placeholder {
        color: $bosch-gray-70;
      }
    }
  }

  &__shadow {
    white-space: pre-wrap;
    height: auto;
    width: 100%;
    position: absolute;
    left: -9999px;
  }


  &--dynamic-height {
    height: 100%;
    // a grid is being used to make it possible that the text area's height would be dynamic,
    // with the data attribute on the after pseudo element
    vertical-align: top;
    align-items: center;
    grid-template-columns: 100% 100%;
  }

  label {
    position: absolute;
    margin: rem(4px) rem(16px) auto rem(16px);
    font-size: rem(12px);
    max-width: calc(100% - 2.25rem);
    white-space: nowrap;
    overflow: auto;
    text-overflow: ellipsis;

    + textarea {
      padding-top: rem(22px);
    }
  }

  .a-ui-icon {
    pointer-events: none;
    position: absolute;
    right: rem(12px);
    bottom: rem(12px);
    display: none;
  }

  &.-success,
  &.-warning,
  &.-error {
    textarea {
      padding-right: rem(48px);
    }

    .a-ui-icon {
      display: inline-block;
    }
  }

  &.-success {
    textarea {
      border-bottom-color: $bosch-green-60;
    }

    .a-ui-icon {

      @extend .a-ui-icon--ui-ic-ok;
    }
  }


  &.-warning {
    textarea {
      border-bottom-color: $bosch-yellow-85;
    }

    .a-ui-icon {

      @extend .a-ui-icon--ui-ic-warning;
    }
  }

  &.-error {
    textarea {
      border-bottom-color: $bosch-red-50;
    }

    .a-ui-icon {

      @extend .a-ui-icon--ui-ic-error;
    }
  }
}
