/* stylelint-disable no-descending-specificity */

@mixin thumb-base {
  margin-top: rem(-11.5px);
  background-color: $bosch-blue-50;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  transition: 0.3s ease-in-out;

  &:hover {
    background-color: $bosch-blue-40;
  }

  &:active {
    background-color: $bosch-blue-30;
  }
}

@mixin track-base {
  // the --progress css variable is set through javascript
  background:
    linear-gradient(
      to right,
      var(--color) 0%,
      var(--color) var(--progress),
      $bosch-gray-80 var(--progress),
      $bosch-gray-80
    );
  height: 0.13rem;
}

@mixin track-base-disabled {
  // the --progress css variable is set through javascript
  background:
    linear-gradient(
      to right,
      $bosch-blue-90 0%,
      $bosch-blue-90 var(--progress),
      $bosch-gray-90 var(--progress),
      $bosch-gray-90
    );
}

.a-slider {
  height: rem(48px);
  width: auto;

  div {
    display: flex;
    position: relative;
    width: 100%;
    flex: 1 1 auto;
    margin: 1.5rem 0.75rem;
  }

  display: flex;
  align-items: center;

  input {
    height: 0.13rem;
    width: 100%;
    -webkit-appearance: none;
    background-color: $bosch-gray-80;
    outline: 0;

    --color: #{$bosch-blue-50};

    &::-webkit-slider-thumb {

      @include thumb-base;

      -webkit-appearance: none;
    }

    &::-webkit-slider-runnable-track {

      @include track-base;

    }

    &::-moz-range-thumb {

      @include thumb-base;

      border: 0;
    }

    &::-moz-range-track {

      @include track-base;
    }


    &:disabled {
      &::-webkit-slider-thumb {
        background-color: $bosch-blue-90;
      }

      &::-moz-range-thumb {
        background-color: $bosch-blue-90;
      }

      &::-webkit-slider-runnable-track {

        @include track-base-disabled;
      }

      &::-moz-range-track {

        @include track-base-disabled;
      }
    }
  }

  .a-slider__tooltip {
    min-width: 4rem;
    text-align: center;
    position: absolute;
    bottom: 1.5rem;
    padding: 0.25rem 0.5rem;
    white-space: nowrap;
    visibility: hidden;
  }

  label {
    font-size: rem(16px);
    flex: 0 1 auto;
  }
}
